<template>

  
  <FinderCore :id = "id" :params="params" visible>
   
  <template v-slot:table>
      <v-simple-table  dense :height="gridHeight" v-if="(nupdate > 0)">
          <template v-slot:default>

              <tbody v-if="(nupdate > 0)">
              <tr 
                v-for="(row, index) in OpenMapData().MainTab"
                :key="index"
                @click="handleClick(index)"
                v-bind:style="{'background-color': (index==current)?selectedColor:'white'}"
              >
                <td>
                    <v-list-item-avatar>
                        <v-img :src="avatar(index)"></v-img>
                    </v-list-item-avatar>
                </td>
                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="350"
                            v-bind:style="{'background-color': (index==current)?selectedColor:'white'}"
                        >
                        <v-list-item  dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["Должность"]}}</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["ПолнИмяСотрудника"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item  dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Табельный номер</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["ТабN"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        </v-card>
                </td>    

                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="300"
                            v-bind:style="{'background-color': (index==current)?selectedColor:'white'}"
                        >
                        <v-list-item dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Компания</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["Организация"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Подразделение</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["Подразделение"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        </v-card>
                </td>


                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="300"
                            v-bind:style="{'background-color': (index==current)?selectedColor:'white'}"
                        >
                        <v-list-item dense>
                            <v-list-item-icon>
                                <v-icon>mdi-home</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["ОфисСотрудника"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item dense>
                            <v-list-item-icon>
                                <v-icon>mdi-phone</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["CorpMobile"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>


                        <v-list-item  dense>
                            <v-list-item-icon>
                                <v-icon>mdi-email-outline</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["Email"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>


                        </v-card>
                </td>
              </tr>
            </tbody>


          </template>
        </v-simple-table>
  </template>    
  
   <template v-slot:editor>
       <v-card
            class="mx-auto"
            max-width="850"
       >
       <table>
           <tr>
               <td style="vertical-align: top;">
                  <v-img
                    max-height="250"
                    max-width="250"
                    :src="avatar(current)"
                  ></v-img>  
               </td>

               <td>

                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{CurrentRow()["Должность"]}}</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{CurrentRow()["ПолнИмяСотрудника"]}}</v-list-item-title>
                            
                                <v-list-item-subtitle>Табельный номер</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{CurrentRow()["ТабN"]}}</v-list-item-title>
                        
                                <v-list-item-subtitle>Компания</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{CurrentRow()["Организация"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item >
                            <v-list-item-content>
                                <v-list-item-subtitle>Подразделение</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{CurrentRow()["Подразделение"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        
               </td>       
           </tr>    
       </table>    

       <v-list-item dense>
            <v-list-item-icon>
            <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
                <v-list-item-subtitle>{{CurrentRow()["ОфисСотрудника"]}}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>

        <v-list-item dense>
            <v-list-item-icon>
                <v-icon>mdi-phone</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
                <v-list-item-subtitle>{{CurrentRow()["CorpMobile"]}}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>


        <v-list-item  dense>
            <v-list-item-icon>
                <v-icon>mdi-email-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
                <v-list-item-subtitle>{{CurrentRow()["Email"]}}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>

       
            <v-card-title>
                 {{CurrentRow()["ПолнИмяСотрудника"]}}   
            </v-card-title>

       </v-card>
   </template>     

  </FinderCore>  
  

</template>
<script>
  import { useMainStore } from '../store/index'
  import FinderCore from "../components2/FinderCore";
  import { mainObj, openMap, baseUrl } from "../main";
  
  export default {
    name: 'UserCust',
    components: {FinderCore},
    setup() {
      const store = useMainStore()
      const menu = []
      store.addItem(menu)
      return { store }
    },
    
    beforeDestroy() {
        const elHtml = document.getElementsByTagName("html")[0];
        elHtml.style.overflowY = "scroll";
    },
    created() {
        const elHtml = document.getElementsByTagName("html")[0];
        elHtml.style.overflowY = "hidden";
    },
    data: () => ({
             params: "1467",
             id: "user_XIXVII",
             current: 0,
             selectedColor: mainObj.selectedColor,  
             nupdate: 1,
             gridHeight: mainObj.gridHeight() 
          }),
    methods: {
        resize: function()
        {
            this.gridHeight = mainObj.gridHeight()
        },
        updateTab: function()
        {   
            this.nupdate = this.nupdate + 1
        },
        OpenMapData: function() {
            return openMap.get(this.id).data;
        },
        CurrentRow: function()
        {
            return openMap.get(this.id).data.MainTab[this.current];
        },
        avatar: function(index) {
            //return "https://randomuser.me/api/portraits/women/" + index.toString() + ".jpg";    
            return baseUrl + "Docfiles/getphoto?audtuser=" + openMap.get(this.id).data.MainTab[index]["AD_Name"];
        },
        handleClick: function(index) {
                //this.OpenMapData().curRow = index;
                this.current = index;
                //вызываем функцию в FinderCore
                openMap.get(this.id).handleClick(index);
            },
    },
    computed: {
        /*
        gridHeight: function()
        {
            return mainObj.gridHeight();
        },
        */
        userSrcImage: function()
        {
            return this.avatar(this.current)
        }
    },
    mounted: function()
    {
        const mid = openMap.get(this.id)
        mid.updateTab = this.updateTab;
        mid.resize = this.resize;
    }    
  }
</script>